.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* 화면 높이 전체를 사용하려면 이 코드를 추가하세요. */
}

.page-title {
  font-size: 2.5em;
}

.page-description {
  font-style: italic;
}

.column-list {
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
}

.column {
  width: 68.75%;
  display: inline-block;
  margin-right: 5%;
}

.image {
  text-align: right;
}

.image img {
  width: 144px;
}

.profile-img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50px;
}